            link: 'https://www.meetup.com/Free-Meditation-Yoga-Munich/events/lxfwtrybclblc/',
            description: '<p>STARTET WIEDER VORORT als Präsenz-Meditation !</p> <p>Für Anfänger &amp; Fortgeschrittene, Einstieg jederzeit möglich</p> <p>With Sahaja Yoga Meditation we generally sit on chairs to achieve Yoga, effortlessly, spontaneously. As such no Asanas (exercises) are required, no mat or special clothing. We are all volunteers of a Charity organisation and have many years of experience in offering free Sahaja Yoga Meditation classes. The classes are designed to help those attending get into a genuine meditative state, which can be felt tangibly. It is quite effortless and spontaneous. The free classes feature introductory talks, video\'s, practical workshops and general question and answer opportunities, making them perfect for seekers trying to establish a state of yoga, and they are available throughout Germany.</p> <p>The idea is that you don\'t do yoga - you Achieve Yoga!</p> <p>Sahaja Yoga Meditation is a simple, time-honored technique. It helps those who enjoy it, reduce stress and increase wellness. It brings better focus and helps people become more centered and better balanced. Anyone can do it. Founded by Shri Mataji Nirmala Devi, Sahaja Yoga Meditation is a world wide movement for peace and self awakening.</p> <p>All the meetings and classes are free of any charge. Each week we go through simple meditation techniques, often with guided meditations to help reduce stress and achieve the peace of true meditation. We share tips on how to help stay in balance during the day and how to meditate at home. The meditation is based on connecting with the inner chakras (energy centres) and balancing the subtle body. The tradition and aims of meditation are explained which in essence is to be in the present with no mental activity or thoughts about the past or the future.</p> <p>Occasionally there are 1 day retreats to help those interested, deepen their individual experience and hear advice and tips from those who have been practicing for some time. It’s ultimately all about getting into ‘thoughtless awareness’, a natural state of being.</p> <p>Most of the times courses are held in German Language, but we do speak english also, some of us can also speak other languages. So don´t be shy to ask.</p> <p>Occasionally there are 1 day retreats to help those interested, deepen their individual experience and hear advice and tips from those who have been practicing for some time. It’s ultimately all about getting into ‘thoughtless awareness’, a natural state of being.</p> <p>**** ENGLISH ****</p> <p>Entdecke die Kraft die in Dir steckt!</p> <p>Herzlich Willkommen zu unseren kostenfreien Treffen. In der Sahaja Yoga Meditation sitzen wir \'normal\' auf Stühlen, um ohne Anstrengung und spontan das Yoga zu erreichen. Es sind keine Matten oder spezielle Kleidung notwendig.</p> <p>Die Kursleiter (Info:[masked]) sind alle ehrenamtlich für den gemeinnützigen Verein Sahaja Yoga e.V. tätig und haben mehrere Jahre Erfahrung in Sahaja Yoga Meditationskursen.</p> <p>Die Kurse sollen den Teilnehmern helfen in einen wirklichen meditativen Zustand zu kommen, der spürbar wahrgenommen werden kann. Es ist sehr spontan und ohne Anstrengung. Die Kurse beinhalten - neben einer geführten Gruppenmeditation - einführende Erklärungen, Videos, Workshops und Gelegenheiten für allgemeine Fragen und Antworten; sie sind perfekt für Sucher die den Zustand des Yoga (= gedankenfreies Bewusstsein / Tao / Sartory / Selbstverwirklichung ...) erreichen wollen.</p> <p>The idea is that you don\'t do yoga - YOU ACHIEVE YOGA!</p> <p>Die Kurse finden in der Regel in deutscher Sprache statt. Bei Bedarf können wir englisch - teilweise auch andere Sprachen.</p> <p>Spontanes Kommen ist möglich, für uns wäre es zum planen gut, wenn ihr hier euer Kommen anmelden würdet.</p> <p>**** Über unser subtiles Sytem ****</p> <p>In jedem Menschen ist ein subtiles System aus Energiezentren (Chakren) und Kanälen (Nadis) angelegt. Es beeinflusst unser körperliches, emotionales, seelisches und geistiges Wohlbefinden, also alle Aspekte unseres Seins. Jedes Chakra ist mit bestimmten menschlichen Eigenschaften und Fähigkeiten verbunden.</p> <p>Durch die Meditation in Sahaja Yoga wird eine in uns schlafende Energie (Kundalini) erweckt. Diese mütterliche Kraft reinigt unsere Chakren und löst Blockaden. Unsere Stärken werden immer stärker und unsere Schwächen werden immer schwächer. Sahaja Yoga ist eine einfache, leicht zu erlernende Meditation. Sie hilft uns, zu entspannen, uns gut zu fühlen und gibt uns unsere innere Balance.</p> <p>Im ersten Satz der UNESCO-Verfassung steht: „Da Kriege im Geist der Menschen entstehen, muss auch der Frieden im Geist der Menschen verankert werden.“ Deshalb ist Sahaja Yoga ein Partner des UNESCO Center for Peace.</p> <p>Die Wechselwirkung zwischen Geist und Körper wird immer mehr erforscht. Wissenschaftliche Studien zeigen, dass regelmäßige Meditation die Gesundheit verbessert. Zum Beispiel gibt es positive Effekte bei Burnout, Depression, Schlaflosigkeit, ADHS, Asthma und bei vielen anderen Problemen. Ängste werden reduziert, die Konzentration gesteigert und die Lebensfreude erhöht.</p> ',
