            description: '<p>topics: data science, product management, r, dashboards</p> <p># DATAMADNESS MUNICH #1 (September)</p> <p>* The Power of Data Product Management (Robert Keil)<br/>* Data Science MVPs with R Shiny (Sarah Stemmler)</p> <p>This event will be in English.</p> <p>Space is limited, please do register for a free ticket.</p> <p>We look forward to seeing you!</p> <p>## Health &amp; Safety Guidelines</p> <p>We hope to make this an offline event but amidst the current development of the pandemic, we really depend on your support. We will share the health and safety guidelines for our event here soon and expect all attendees to follow the instructions. Thanks for your cooperation!</p> <p>## Agenda</p> <p>18:00 – Doors open.</p> <p>18:30 – Welcome.</p> <p>18:45 – The Power of Data Product Management.</p> <p>19:30 – Break with pizza and cold drinks.</p> <p>20:00 – Data Science MVPs with R Shiny.</p> <p>20:45 – Open mic.</p> <p>21:00 – End.</p> <p>## Location</p> <p>The event will take place at the Usercentrics office in the heart of Munich. We will put up signs so you can\'t miss us.</p> <p>## Open mic</p> <p>The end of the event is dedicated to our attendees who want to share something they care about with the community. Any content is welcome, as long as it fits in three minutes and does not advertise paid products or services. If you want to reserve a spot, please reach out.</p> <p>## Acknowledgement</p> <p>Thanks a lot to our speakers Robert and Sarah!</p> <p>Also a big thank you to our sponsors:</p> <p>* Instaffo, for supporting the organization.<br/>* Usercentrics, for hosting the event.</p> <p>## Contact</p> <p>If you have any questions or suggestions, please feel free to contact us via:</p> <p>* Meetup<br/>* Telegram: <a href=\'https://t.me/joinchat/Lk5_ABrLl6xQiKXhBOI67w\' class=\'linkified\'>https://t.me/joinchat/Lk5_ABrLl6xQiKXhBOI67w</a></p> ',
