            description: '<p>+++BEACHTE+++Aufgrund der begrenzten Plätze ist zur kostenlosen Teilnahme eine vorherige und verbindliche Anmeldung unter <a href=\'https://usability-testessen.org/stadt/region-rosenheim/\' class=\'linkified\'>https://usability-testessen.org/stadt/region-rosenheim/</a> notwendig.+++</p> <p>Liebe Testessen-Community,</p> <p>wir möchten euch weiterhin die Möglichkeit bieten eure Prototypen, Websites o. ä. auf ihre Gebrauchstauglichkeit zu testen. Weil es das letzte Mal schon so gut remote geklappt hat, haben wir uns dazu entschieden, das nächste Usability Testessen nochmal auf ein Webmeeting (ZOOM) zu verlagern!</p> <p>Damit das Verhältnis von Testpersonen und Testobjekten passt, werden wir die Anmeldebestätigungen inklusive Zugangsdaten zum ZOOM Meeting manuell versenden. Wer etwas zum Testen mitbringt, hat eine höhere Wahrscheinlichkeit einen Platz zu ergattern. Wer keine Bestätigung von uns erhält, steht automatisch auf der Warteliste und wird – sobald ein Platz frei wird – von uns informiert.</p> <p>+++++++++++++++++++++++++++++++++++++++++++++</p> <p>Seit Juni 2016 veranstalten wir das Usability-Testessen im Südostbayerischen Raum (Rosenheim und Umland). Die bisherige Resonanz ist extrem positiv.</p> <p>Das Ganze funktioniert relativ simpel, die Resultate sind häufig aber umso erstaunlicher: In einer Art Speed-Dating, stellen Unternehmen Ihre Webseite, Ihren Online-Shop, Ihre Software o.ä. mit konkreten Fragestellungen Testpersonen vor, die das Ganze dann testen und ehrliches und transparentes Feedback zur Bedienbarkeit (Usability) geben.</p> <p>Mehr Infos dazu gibt´s unter <a href=\'https://www.techdivision.com/blog/erstes-usability-testessen-in-sued-ost-bayern/\' class=\'linkified\'>https://www.techdivision.com/blog/erstes-usability-testessen-in-sued-ost-bayern/</a></p> <p>Am Donnerstag,[masked] findet das nächste Usability-Testessen statt.<br/>Sowohl für Testgeber als auch Tester ist das Ganze absolut kostenlos und unverbindlich. Beginn der Veranstaltung ist dieses Mal um 18 Uhr.</p> <p>Jetzt anmelden und echtes User-Feedback einholen!<br/><a href=\'https://usability-testessen.org/stadt/region-rosenheim/\' class=\'linkified\'>https://usability-testessen.org/stadt/region-rosenheim/</a></p> ',
