            link: 'https://www.meetup.com/Rosenheimer-New-Work-Meetup/events/272002824/',
            description: '<p>Wir freuen uns enorm, dass wir für unser New Work Meetup den bekannten Coach, Trainer, Berater und Autor Torsten Scheller gewinnen konnten, der über das spannende und top-aktuelle Thema der Wertstrom-Organisation sprechen wird.</p> <p>Funktionierende Teams allein reichen nicht, denn viele funktionierende Teams führen zu … vielen lokalen Optima. Warum eigentlich? Warum führen viele funktionierende Teams nicht zu seiner funktionierenden Organisation? Woran scheitern auch agile Organisationen? Wie müssen Organisationen aufgestellt sein, damit die Leistung der Organisation wirklich den Kunden begeistert?</p> <p>Diese Fragen führten Torsten zu einer Reise durch den trockenen Kontinent der Organisationslehre mit seiner Wüste namens „Die Taylor-Ford-Organisation“, vorbei an der Quelle „Wertschöpfung“ zum Delta des reißenden Wertstroms. Das Ergebnis seiner Reise sind Erkenntnisse (in Form eines Buches). Einen Einblick dazu möchte er Euch an diesem Abend geben.</p> <p>Inhalt: Von Organisatorischen Schulden über die beiden Grundprobleme heutiger Organisationen über Wertströme zur Struktur einer wertstrombasierten Organisation.</p> <p>Nutzen:</p> <p>- Ihr habt ein tieferes Verständnis davon, woran Organisation wirklich leiden.</p> <p>- Ihr versteht Wertschöpfung – und wie man diese organisieren muss – besser.</p> <p>- Ihr habt eine Idee davon, wie eine wertstrombasierte Organisation aussehen kann.</p> <p>Über Torsten Scheller<br/>Sein Ziel sind Rahmenbedingungen, in denen Menschen ihr Potenzial zum Nutzen aller entfalten. Funktionierende Organisationen sind ein erster Schritt zur Verwicklung seiner Vision: „Wir RENNEN am Morgen FREUDIG zu unserer Arbeit und ENTFALTEN KOOPERATIV unser Potenzial, um unsere KUNDEN mit herausragenden innovativen Produkten und Services ZU BEGEISTERN.“</p> ',
