            description: '<p>Wie Du wirklich funktionierende Kundenbeziehungen erreichst</p> <p>Unternehmen verlieren durch externe Marktvorgänge oder interne Unternehmensentwicklungen das Verständnis für den Kunden. Dabei werden langfristige Kundenbeziehungen als „gegeben“ angesehen. Dies ist fatal und wirkt sich auf Marketing, Vertrieb sowiedie Produktgenese aus.</p> <p>Wir entschlüsseln Kundenbedarfe durch Customer Development und geben daraus Impulse für Marketing, Sales und die Produktentwicklung. Sparekünftigmessbar an Kundenakquise-Kosten (CAC) bei gleichzeitigem Steigerndes Kundenwertes (LTV).</p> <p>Mögliche Anwendungsfälle:<br/>•Markteintritte und Produktlaunches schärfen<br/>•CRM Hürden und Chancen identifizieren<br/>•Kundenzentrierte Produktentwicklung</p> <p>Zielmarkt&amp; Zielpersonen<br/>Geschäftsführer, Marketing-und Produkt-Entscheider in Mittelstandsunternehmen im B2B Umfeld(Umsatz/Jahr&gt; 5Mio. €), welche die digitale Transformation als Chance für ihren Unternehmenserfolg begreifen. Gründer und Geschäftsführer finanzierter Startups oder Scale-ups im B2B Umfeld, die in kürzester Zeit explosiv wachsen wollen bzw. müssen.</p> <p>Referenten: André Wehr und Manuel Schmidt (Gründer tractionwise)</p> <p>Anmeldung bitter per Email an info(at)link-landshut.de.</p> ',
